
export function InfoPreview({ info }) {
    return (
        <div className="info-preview flex">
            <img className="info-img" src={info.type} />
            <div className="text-section flex column align-center justify-center">
                <h4>{info.header}</h4>
                <p>{info.para}</p>
            </div>
        </div>
    )
}