import quote from '../styles/imgs/icon-quotes.svg'

export function DeliveringPreview({ info }) {
    return (
        <section className="deliver-preview">
            <img className="quote-img" src={quote} />
            <p>{info.para}</p>
            <h5>{info.fullname}</h5>
            <h6>{info.job}</h6>
            <img className="per-img" src={info.img} />
        </section>
    )
}