
import heroleft from '../styles/imgs/bg-pattern-home-1.svg'
import herobot from '../styles/imgs/bg-pattern-home-2.svg'
export function AppHeader() {
    return (
        <section className="app-header main-container full">
            <section className="flex space-between justify-center align-center header-section">
                <div className="section-right flex">
                    <div className="logo flex align-center justify-center">myteam</div>
                    <div className="nav-links flex align-center">
                        <div>
                            home
                        </div>
                        <div>
                            about
                        </div>
                    </div>
                </div>
                <button className="contact-btn">
                    contact-us
                </button>
            </section>
            <div className="hero flex align-center space-between">
                <img className="img-left" src={heroleft} />
                <div className="talent">Find the best <span>talent</span></div>
                <div className="info flex column">
                    <p>Finding the right people and building high performing teams can be hard. Most companies aren’t tapping into the abundance of global talent. We’re about to change that.</p>
                    <img className="img-bot" src={herobot} />
                </div>
            </div>
        </section>
    )
}