import kady from '../styles/imgs/avatar-kady.jpg'
import arthur from '../styles/imgs/avatar-arthur.jpg'
import aiysha from '../styles/imgs/avatar-aiysha.jpg'
import { DeliveringList } from './delivering-list'
import square from '../styles/imgs/square.svg'
import botright from '../styles/imgs/bg-pattern-home-5.svg'
const list = [
    {
        fullname: 'Kady Baker',
        job: 'Product Manager at B',
        para: ' “The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.”',
        img: kady
    },
    {
        fullname: 'Aiysha Reese',
        job: 'Founder of Manage',
        para: '“We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!”',
        img: aiysha
    },
    {
        fullname: 'Arthur Clarke',
        job: 'Co-founder of MyPhys',
        para: '“Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.”',
        img: arthur
    }
]

export function Delivering() {
    return (
        <section className="main-container full">
                <section className="delivering flex column align-center">
                    <img className="square-img"src={square}/>
                    <h2>Delivering real results for top companies. Some of our <span>success stories.</span></h2>
                    <DeliveringList infos={list} />
                    <img className="bot-right-img" src={botright}/>
            </section>
        </section>
    )
}